/**
 * An operation group is a aggregation of operations that each one of them point to a different location.
 * Each operation group has different ordering page.
 */
export interface OperationGroup {
  /**
   * OperationsGroup ID.
   * @format GUID
   * @readonly
   */
  id?: string | null;
  /**
   * Revision number, which increments by 1 each time the OperationsGroup is updated.
   * To prevent conflicting changes,
   * the current revision must be passed when updating the OperationsGroup.
   *
   * Ignored when creating a OperationsGroup.
   * @readonly
   */
  revision?: string | null;
  /**
   * Date and time the OperationsGroup was created.
   * @readonly
   */
  createdDate?: Date | null;
  /**
   * Date and time the OperationsGroup was last updated.
   * @readonly
   */
  updatedDate?: Date | null;
  /**
   * The name of the operations group
   * @minLength 1
   * @maxLength 500
   */
  name?: string | null;
  /** Data Extensions */
  extendedFields?: ExtendedFields;
  /** Tags ([SDK](https://dev.wix.com/docs/sdk/backend-modules/tags/tags/introduction) | [REST](https://dev.wix.com/docs/rest/business-management/tags/introduction)) used to classify and sort different types of operation groups. */
  tags?: Tags;
}

export interface ExtendedFields {
  /**
   * Extended field data. Each key corresponds to the namespace of the app that created the extended fields.
   * The value of each key is structured according to the schema defined when the extended fields were configured.
   *
   * You can only access fields for which you have the appropriate permissions.
   *
   * Learn more about [extended fields](https://dev.wix.com/docs/rest/articles/getting-started/extended-fields).
   */
  namespaces?: Record<string, Record<string, any>>;
}

/**
 * Common object for tags.
 * Should be use as in this example:
 * message Foo {
 * string id = 1;
 * ...
 * Tags tags = 5
 * }
 *
 * example of taggable entity
 * {
 * id: "123"
 * tags: {
 * tags: {
 * tag_ids:["11","22"]
 * },
 * private_tags: {
 * tag_ids: ["33", "44"]
 * }
 * }
 * }
 */
export interface Tags {
  /** Tags that require an additional permission in order to access them, normally not given to site members or visitors. */
  privateTags?: TagList;
  /** Tags that are exposed to anyone who has access to the labeled entity itself, including site members and visitors. */
  tags?: TagList;
}

export interface TagList {
  /**
   * List of tag IDs
   * @maxSize 100
   * @maxLength 5
   */
  tagIds?: string[];
}

